  copiedText () {
    this.setState({
      ...this.state,
      copied: true
    })

    setTimeout(() => {
      this.setState({
        ...this.state,
        copied: false
      })
    }, 2000);
  }

  updateForm (event, name) {
    let value = event.target.value;
    console.log(name)
    if(name === 'accountName'){
      this.setState({
        ...this.state,
        buyOrderConfirm: {...this.state.buyOrderConfirm, accountName: value}
      })
    }else if(name === 'accountNumber'){
      this.setState({
        ...this.state,
        buyOrderConfirm: {...this.state.buyOrderConfirm, accountNumber: value}
      })
    }else if(name === 'networkTransactionId'){
      this.setState({
        ...this.state,
        transactionNumber: value,
        buyOrderConfirm: {...this.state.buyOrderConfirm, networkTransactionId: value}
      })
    }
  }

