  getUserPaymentMethod () {
    this.setState({
      ...this.state,
      isLoading: true
    })
    axios.get(`${Constants.API_BASE_URL}/users/payment/method`, AuthService.getAxiosHeaders())
    .then(res=>{
      this.setState(prev=>{return{
      ...prev,
      isLoading: false,
      paymentMethod: {
        ...res.data,
        accountName: res.data.accountName,
        network: res.data.network,
        accountNumber: res.data.phoneNumber
      },
      buyOrderConfirm: {
        ...this.state.buyOrderConfirm,
        accountNumber: res.data.phoneNumber,
        accountName: res.data.accountName
      }
    }})
    console.log(res)
    }).catch(()=>{
      this.setState({
      ...this.state,
      isLoading: false
    })
    })
  }

