import React, { Fragment } from "react";
import MetaTags from "react-meta-tags";
import AuthService from '../../misc/AuthService';
import CONSTANTS from '../../misc/Constants';
import cookie from 'react-cookies';
import axios from 'axios';
import { reactLocalStorage } from "reactjs-localstorage";

class Register extends React.Component {

  constructor(props){
	AuthService.checkGreeter();
    super(props);
    this.state = {
		isLoading: true,
		isRequesting: false,
		showPassword: false,
		registrationForm: {
			firstName: '',
			lastName: '',
			email: '',
			password: ''
		},
		registrationFormErrors: {
			firstName: '',
			lastName: '',
			email: '',
			password: ''
		},
		errorMessage: ''
    }
	}
	
	registrationForm(event, name) {
    let value = event.target.value;
    var oldData = this.state.registrationForm;
    oldData[name] = value;
    this.setState({
		...this.state,
    registrationForm: oldData,
    });
	}
	
	submitRegistrationForm = e => {
		this.setState({
			...this.state,
			isRequesting: true,
			errorMessage: '',
			registrationFormErrors: {
				firstName: '',
				lastName: '',
				email: '',
				password: ''
		}
		});

		e.preventDefault();
		
		axios.post(CONSTANTS.API_BASE_URL + "/auth/user/register", {...this.state.registrationForm})
		.then((response) => {
			localStorage.setItem('toptop', JSON.stringify(response))
			console.log(response)
			let data = response.data;
      		
			cookie.save('access_token', response.data.token)
			cookie.save('refresh_token', response.data.refreshToken)
			reactLocalStorage.setObject('userObj', {phoneIdentity: data.phoneIdentity, emailIdentity: data.emailIdentity, user: data.user, accountStatus: true});
			cookie.save('userObj', {phoneIdentity: data.phoneIdentity, emailIdentity: data.emailIdentity, user: data.user, accountStatus: true})
			
			window.location = "/email-verification/check/" + this.state.registrationForm.email;
		}).catch((error) => {
			console.log(error)
			try{
				let errorResponse = error.response.data;
						let registrationFormErrors = this.state.registrationFormErrors;

						if(errorResponse.hasOwnProperty("errors")){
							if(errorResponse.errors.hasOwnProperty("firstName")){
								registrationFormErrors.firstName = errorResponse.errors.firstName;
							}

							if(errorResponse.errors.hasOwnProperty("lastName")){
								registrationFormErrors.lastName = errorResponse.errors.lastName;
							}

							if(errorResponse.errors.hasOwnProperty("email")){
								registrationFormErrors.email = errorResponse.errors.email;
							}

							if(errorResponse.errors.hasOwnProperty("password")){
								registrationFormErrors.password = errorResponse.errors.password;
							}
						}

						let errorMessage = 'Error: Could not connect to server';
						if(errorResponse.hasOwnProperty("message")){
							errorMessage = errorResponse.message;
						}

						this.setState({
							...this.state,
							isRequesting: false,
							errorMessage: errorMessage,
							registrationFormErrors: registrationFormErrors
						});
			}catch(e){
				// window.location = "/server-offline";
			}
		});
		
		

	}

  componentDidMount (){
		// window.initScript(window.NioApp, window.jQuery);
		setTimeout(() => {
			this.setState({
				...this.state,
				isLoading: false
			})
		}, 1000);
  }

  render(){
    return (
      <Fragment>
          <MetaTags>
          <title>Sign Up - HodlPay</title>
          </MetaTags>
					
						<div className="d-flex flex-column flex-root">
						{
							this.state.isLoading?
							<div className="pre-loader">
								<div className="text-center">  
									<div className="spinner-border" role="status">    
										<span className="sr-only">Loading...</span>  
									</div>
								</div>
							</div>
							:
							<div className="d-flex flex-column flex-column-fluid bgi-position-y-bottom position-x-center bgi-no-repeat bgi-size-contain bgi-attachment-fixed">
								<div className="d-flex flex-center flex-column flex-column-fluid p-10 pb-lg-20">
									<a href="/login" className="mb-10 pt-lg-10">
										<img alt="Logo" src="/assets/media/logos/hodlpay-black.svg" className="h-70px" />
									</a>
									<div className="w-lg-600px bg-body rounded shadow-sm p-10 p-lg-15 mx-auto">
										<div className="mb-10 text-center">
											<h1 className="text-dark mb-3">Create an Account</h1>
											<div className="text-gray-400 fw-bold fs-4">Already have an account? 
											<a href="/login" className="link-primary fw-bolder"> Sign in here</a></div>
										</div>
										
										<form onSubmit={this.submitRegistrationForm} method="POST">
											{
												this.state.errorMessage.length > 0 &&
												<div className="alert alert-danger d-flex align-items-center p-5 mb-10 w-100"> 
													<span className="svg-icon svg-icon-2hx svg-icon-danger me-4">
														<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
															<g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
																	<rect x="0" y="0" width="24" height="24"/>
																	<circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"/>
																	<rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"/>
																	<rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"/>
															</g>
														</svg>
													</span>
													<div className="d-flex flex-column">
														<h4 className="mb-1 text-danger">Error</h4> <span>{this.state.errorMessage}</span> </div>
												</div>
											}
											<div className="row fv-row">
												<div className="col-xl-6 mb-7">
													<label className="form-label fw-bolder text-dark fs-6">First Name</label>
													<input className="form-control form-control-lg form-control-solid" type="text" placeholder="" name="first-name" autoComplete="false" value={this.state.registrationForm.firstName} onChange={(e) => {this.registrationForm(e,"firstName");}} />
													{
														this.state.registrationFormErrors.firstName.length > 0 && 
														<p className="text-danger fs-12px">{this.state.registrationFormErrors.firstName}</p>
													}
												</div>
												<div className="col-xl-6 mb-7">
													<label className="form-label fw-bolder text-dark fs-6">Last Name</label>
													<input className="form-control form-control-lg form-control-solid" type="text" placeholder="" name="last-name" autoComplete="false" value={this.state.registrationForm.lastName} onChange={(e) => {this.registrationForm(e,"lastName");}} />
													{
														this.state.registrationFormErrors.lastName.length > 0 && 
														<p className="text-danger fs-12px">{this.state.registrationFormErrors.lastName}</p>
													}
												</div>
											</div>
											<div className="fv-row mb-7">
												<label className="form-label fw-bolder text-dark fs-6">Email</label>
												<input className="form-control form-control-lg form-control-solid" type="email" placeholder="" name="email" autoComplete="false" value={this.state.registrationForm.email} onChange={(e) => {this.registrationForm(e,"email");}} />
												{
													this.state.registrationFormErrors.email.length > 0 && 
													<p className="text-danger fs-12px">{this.state.registrationFormErrors.email}</p>
												}
											</div>
											<div className="mb-10 fv-row" data-kt-password-meter="true">
												<div className="mb-1">
													<label className="form-label fw-bolder text-dark fs-6">Password <small className="text-muted">(Use 8 or more characters with a mix of letters, numbers &amp; symbols.)</small></label>
													<div className="position-relative mb-3">
														<input className="form-control form-control-lg form-control-solid" type={this.state.showPassword ? "text" : "password"} placeholder="" name="password" autoComplete="false" value={this.state.registrationForm.password} onChange={(e) => {this.registrationForm(e,"password");}} />
														<span className="btn btn-sm btn-icon position-absolute translate-middle top-50 end-0 me-n2" data-kt-password-meter-control="visibility" onClick={() => {
															this.setState({
																...this.state,
																showPassword: this.state.showPassword ? false : true
															})
														}}>
															{
																this.state.showPassword ?
																<i className="bi bi-eye-slash fs-2"></i>
																:
																<i className="bi bi-eye fs-2"></i>
															}
														</span>
													</div>
													{
														this.state.registrationFormErrors.password.length > 0 && 
														<p className="text-danger fs-12px">{this.state.registrationFormErrors.password}</p>
													}
												</div>
											</div>
											<div className="fv-row mb-10 text-center">
												<label className="form-label">
													<span className="form-check-label fw-bold text-gray-700 fs-6">By creating an account I agree to HodlPay's 
													<small className="ms-1 link-primary">Terms and conditions</small>.</span>
												</label>
											</div>
											<div className="text-center">
												{
													this.state.isRequesting ?
													<button type="button" className="btn btn-lg btn-primary" disabled>
														<span className="">Please wait... 
														<span className="spinner-border spinner-border-sm align-middle ms-2"></span></span>
													</button>
													:
													<button type="submit" className="btn btn-lg btn-primary">
														<span className="indicator-label">Create Account</span>
													</button>
												}
												
											</div>
										</form>
									</div>
								</div>
								<div className="d-flex flex-center flex-column-auto p-10">
									<div className="d-flex align-items-center fw-bold fs-6">
										<a href={`${CONSTANTS.WEB_URL}/#about`} target="_blank" className="text-muted text-hover-primary px-2" rel="noreferrer">About</a>
										<a href={`${CONSTANTS.WEB_URL}/terms-and-conditions`} target="_blank" className="text-muted text-hover-primary px-2" rel="noreferrer">Terms and Conditions</a>
										<a href={`${CONSTANTS.WEB_URL}/privacy-policy`} target="_blank" className="text-muted text-hover-primary px-2" rel="noreferrer">Privacy Policy</a>
									</div>
								</div>
							</div>
						}
						</div>
      </Fragment>
    );
  }
}

export default Register;