import React, { Fragment } from "react";
import MetaTags from "react-meta-tags";
import AuthService from '../../misc/AuthService';
import OrderForm from "./include/OrderForm";
import OrderInfo from "./include/OrderInfo";
import {TableLoader} from "./include/Loaders";
import axios from 'axios';
// import 'antd/dist/antd.css';
import SideNav from "./include/SideNav";
import Header from "./include/Header";
import Footer from "./include/Footer";
import DatePicker from "react-datepicker";
import moment from 'moment';

import "react-datepicker/dist/react-datepicker.css";
import Constants from "../../misc/Constants";
import format from "../../misc/formatData";
import refreshToken from "../../misc/RefreshToken";
import Pagination1 from "./include/Pagination1";

class Orders extends React.Component {

  constructor(props){
		AuthService.checkProtected();
    super(props);
    this.state = {
      userInfo: AuthService.getAuth().user,
			isLoading: false,
			isRequesting: false,
      showFilter: false,
      orderType: 'buy',
      orderID: '',
      showOrderInfo: false,
      page: 1,
      range: null,
      rangeStart: null,
      rangeEnd: null,
      search: '',
      status: 'all',
      type: 'all',
      currency: 'all',
      currentPage: 1,
      totalList: 1,
      orders: [],
      total: 0,
      passedOrder: {},
    }
	}


  onChangeRangePicker = (value) => {
    let range = value;
    let rangeStart = range !== null ? range[0] : null;
    let rangeEnd = range !== null ? range[1] : null;
    range = value === null ? null : moment(rangeStart).format("YYYY-MM-DD")+','+moment(rangeEnd).format("YYYY-MM-DD");
    this.setState({
      ...this.state,
      range: range,
      rangeStart: rangeStart,
      rangeEnd: rangeEnd
    });
  }

  filterForm(event, name) {
    let value = event.target.value;

    this.setState({
      ...this.state,
      page: 1,
      [name]: value,
    });
	}

  fetchByPagination (count) {
    this.setState({
      ...this.state,
      isRequesting: true
    })
    console.log('djfkdjfk', count)
    // Getting user orders ...
    axios.get(`${Constants.API_BASE_URL}/users/orders?limit=5&skip=${count}`, AuthService.getAxiosHeaders())
    .then(res=>{
      let data = res.data.data;
      console.log(data, res)
      this.setState({
        ...this.state,
        isLoading: false,
        isRequesting: false,
        currentPage: this.state.page,
        totalList:  Math.ceil(data?.meta?.result_total / 10),
        orders: [...data],
        total: res.data?.total
      });
    }).catch((err)=>{
      this.setState({
        ...this.state,
        isRequesting: false
      })
      if(err.response.status === 401){
        refreshToken();
      }
    })
    return
  }

  getOrders () {
    this.setState({
      ...this.state,
      isRequesting: true
    })

    // Getting user orders ...
    axios.get(`${Constants.API_BASE_URL}/users/orders`, AuthService.getAxiosHeaders())
    .then(res=>{
      let data = res.data.data;
      console.log(data, res)
      this.setState({
        ...this.state,
        isLoading: false,
        isRequesting: false,
        currentPage: this.state.page,
        totalList:  Math.ceil(data?.meta?.result_total / 10),
        orders: [...data],
        total: res.data?.total
      });
    }).catch(err=>{
      console.log({...err})
      // refreshToken();
      this.setState({
        ...this.state,
        isRequesting: false
      })
      if(err.response.status===401){
        window.location = '/logout'
      }
    })


    // axios.get(CONSTANTS.API_BASE_URL + "users/orders", AuthService.getAxiosHeaders())
    // .then((response) => {
    //   let responseInfo = response.data;
    //   console.log(response.data)
    //   // this.setState({
    //   //   ...this.state,
    //   //   isLoading: false,
    //   //   isRequesting: false,
    //   //   currentPage: this.state.page,
    //   //   totalList:  Math.ceil(responseInfo.data.meta.result_total / 10),
    //   //   orders: responseInfo.data
    //   // });
    // }).catch((error) => {
    //   console.log(error.response)
    //   // try{
    //   //   let errorResponse = error.response.data;

    //   //   if(error.response.status === 401 || error.response.status === 404){
    //   //     window.location = "/not-found";
    //   //     return;
    //   //   }

    //   //   let errorMessage = 'Error: Could not connect to server';
    //   //   if(errorResponse.hasOwnProperty("message")){
    //   //     errorMessage = errorResponse.message;
    //   //   }

    //   //   this.setState({
    //   //     ...this.state,
    //   //     isLoading: false,
    //   //     isRequesting: false,
    //   //     errorMessage: errorMessage
    //   //   });
    //   // }catch(e){
    //   //   console.log(error)
    //   //   // window.location = "/server-offline";
    //   // }
    // });
  }

  showOrderInfo = (orderID) => {
    this.setState({
      ...this.state,
      orderID: orderID,
      showOrderInfo: true
    })

    setTimeout(() => {
      this.getOrders ();
      window.modalShow('order_info_modal');
    }, 500);
  }

  updateOrders = (orderAction) => {
    if(orderAction){
      this.getOrders ();
    }
    
    this.setState({
      ...this.state,
      showOrderInfo: false
    })
  }

  componentDidMount () {
    this.getOrders();
  }
	

  render(){
    return (
      <Fragment>
          <MetaTags>
          <title>Orders - HodlPay</title>
          </MetaTags>
          <div className="header-fixed header-tablet-and-mobile-fixed aside-fixed aside-secondary-disabled body">
            <div className="d-flex flex-column flex-root">
        
              <div className="page d-flex flex-row flex-column-fluid">
                
                <SideNav active="orders" />
                
                
                <div className="wrapper d-flex flex-column flex-row-fluid" id="kt_wrapper">
                  
                  <div className="header-mobile py-3">
                    
                    <div className="container d-flex flex-stack">
                      
                      <div className="d-flex align-items-center flex-grow-1 flex-lg-grow-0">
                        <a href="index.html">
                          <img alt="Logo" src="/assets/media/logos/hodlpay-black.svg" className="h-35px" />
                        </a>
                      </div>
                      
                      
                      <button className="btn btn-icon btn-active-color-primary" id="kt_aside_toggle">
                        
                        <span className="svg-icon svg-icon-2x me-n1">
                          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                            <path d="M21 7H3C2.4 7 2 6.6 2 6V4C2 3.4 2.4 3 3 3H21C21.6 3 22 3.4 22 4V6C22 6.6 21.6 7 21 7Z" fill="black" />
                            <path opacity="0.3" d="M21 14H3C2.4 14 2 13.6 2 13V11C2 10.4 2.4 10 3 10H21C21.6 10 22 10.4 22 11V13C22 13.6 21.6 14 21 14ZM22 20V18C22 17.4 21.6 17 21 17H3C2.4 17 2 17.4 2 18V20C2 20.6 2.4 21 3 21H21C21.6 21 22 20.6 22 20Z" fill="black" />
                          </svg>
                        </span>
                        
                      </button>
                      
                    </div>
                    
                  </div>
                  
                  <Header title={`Orders`} />

                  {
                    this.state.isLoading ?
                    <div className="pre-loader">
                      <div className="text-center">  
                        <div className="spinner-border" role="status">    
                          <span className="sr-only">Loading...</span>  
                        </div>
                      </div>
                    </div>
                    :
                    <>
                      
                      <div className="content d-flex flex-column flex-column-fluid" id="kt_content">
                        
                        <div className="container-xxl" id="kt_content_container">
                          <div className="card mb-7">
                            <div className="card-body">
                              <div className="d-flex align-items-center">
                                <div className="position-relative w-md-400px me-md-2"> 
                                    <span className="svg-icon svg-icon-3 svg-icon-gray-500 position-absolute top-50 translate-middle ms-6">
                                      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                                        <rect opacity="0.5" x="17.0365" y="15.1223" width="8.15546" height="2" rx="1" transform="rotate(45 17.0365 15.1223)" fill="black" />
                                        <path d="M11 19C6.55556 19 3 15.4444 3 11C3 6.55556 6.55556 3 11 3C15.4444 3 19 6.55556 19 11C19 15.4444 15.4444 19 11 19ZM11 5C7.53333 5 5 7.53333 5 11C5 14.4667 7.53333 17 11 17C14.4667 17 17 14.4667 17 11C17 7.53333 14.4667 5 11 5Z" fill="black" />
                                      </svg>
                                    </span>
                                  <input type="text" className="form-control form-control-solid ps-10" name="search" onChange={(e) => {this.filterForm(e,"search");}} value={this.state.search ?? ''} placeholder="Search" /> 
                                </div>
                                <div className="d-flex align-items-center">
                                  <button onClick={() => {this.getOrders()}} className="btn btn-primary me-5">Search</button> 
                                  <a id="kt_horizontal_search_advanced_link" className="btn btn-link" data-bs-toggle="collapse" href="#kt_advanced_search_form" onClick={() => {
                                    this.setState({
                                      ...this.state,
                                      // showFilter: this.state.showFilter ? false : true
                                    })
                                  }}>{this.state.showFilter && `Hide`} Advanced Search</a> 
                                </div>
                              </div>
                              <div className="collapse" id="kt_advanced_search_form--">
                                <div className="separator separator-dashed mt-9 mb-6"></div>
                                <div className="row g-8 mb-8">
                                  <div className="col-xxl-6">
                                    <div className="row g-8">
                                      <div className="col-lg-6">
                                        <label className="fs-6 form-label fw-bolder text-dark">Order Type</label>
                                        <div className="nav-group nav-group-fluid">
                                          <label>
                                            <input type="radio" className="btn-check" name="type" value="all" onChange={(e) => {this.filterForm(e,"type");}} checked={this.state.type==='all'} /> 
                                            <span className="btn btn-sm btn-color-muted btn-active btn-active-primary fw-bolder px-4">All</span> 
                                          </label>
                                          <label>
                                            <input type="radio" className="btn-check" name="type" value="buy" onChange={(e) => {this.filterForm(e,"type");}} checked={this.state.type==='buy'} /> 
                                            <span className="btn btn-sm btn-color-muted btn-active btn-active-primary fw-bolder px-4">Buy</span> 
                                          </label>
                                          <label>
                                            <input type="radio" className="btn-check" name="type" value="sell" onChange={(e) => {this.filterForm(e,"type");}} checked={this.state.type==='sell'} /> 
                                            <span className="btn btn-sm btn-color-muted btn-active btn-active-primary fw-bolder px-4">Sell</span> 
                                          </label>
                                        </div>
                                      </div>
                                      <div className="col-lg-6">
                                        <label className="fs-6 form-label fw-bolder text-dark">Order Status</label>
                                        <select className="form-select form-select-solid" value={this.state.status} data-control="select2" onChange={(e) => {this.filterForm(e,"status");}} data-hide-search="true">
                                          <option value="all">All</option>
                                          <option value="pending_payment">Pending Payment</option>
                                          <option value="pending_transfer">Pending Transfer</option>
                                          <option value="pending_verification">Pending Verification</option>
                                          <option value="pending_payout">Pending Payout</option>
                                          <option value="completed">Completed</option>
                                          <option value="failed">Failed</option>
                                        </select>
                                      </div>
                                    </div>
                                  </div>
                                  <div className="col-xxl-6">
                                    <div className="row g-8">
                                      <div className="col-lg-6">
                                        <label className="fs-6 form-label fw-bolder text-dark">Currency</label>
                                        <select className="form-select form-select-solid" value={this.state.currency} data-control="select2" onChange={(e) => {this.filterForm(e,"currency");}} data-hide-search="true">
                                          <option value="all">All</option>
                                          <option value="Bitcoin">Bitcoin</option>
                                          <option value="USDT">USDT</option>
                                        </select>
                                      </div>
                                      <div className="col-lg-6">
                                        <label className="fs-6 form-label fw-bolder text-dark">Date Range</label>
                                        {/* <input type="text" className="form-control form-control form-control-solid" name="tags" value="products, users, events" />  */}
                                        <DatePicker
                                          selectsRange={true}
                                          startDate={this.state.rangeStart}
                                          endDate={this.state.rangeEnd}
                                          // customInput={<CustomDateInput />}
                                          onChange={(update) => {
                                            this.onChangeRangePicker(update)
                                          }}
                                        />
                                      </div>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                          {
                            this.state.isRequesting ?
                            <TableLoader></TableLoader>
                            :
                            <>
                            <div className="d-flex flex-wrap flex-stack pb-7">
                              <div className="d-flex flex-wrap align-items-center my-1">
                                <h3 className="fw-bolder me-5 my-1">{this.state.total} Orders Found</h3>
                              </div>
                            </div>
                            <div className="card card-flush">
                              <div className="card-body pt-0">
                              {
                                this.state.orders?.length === 0 ?
                                <div className="text-center py-20">
                                  <span className="symbol">
                                    <span className="svg-icon svg-icon-3x svg-icon-primary">
                                      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                                        <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                          <rect x="0" y="0" width="24" height="24"/>
                                          <rect fill="#000000" opacity="0.3" x="2" y="2" width="10" height="12" rx="2"/>
                                          <path d="M4,6 L20,6 C21.1045695,6 22,6.8954305 22,8 L22,20 C22,21.1045695 21.1045695,22 20,22 L4,22 C2.8954305,22 2,21.1045695 2,20 L2,8 C2,6.8954305 2.8954305,6 4,6 Z M18,16 C19.1045695,16 20,15.1045695 20,14 C20,12.8954305 19.1045695,12 18,12 C16.8954305,12 16,12.8954305 16,14 C16,15.1045695 16.8954305,16 18,16 Z" fill="#000000"/>
                                        </g>
                                      </svg>
                                    </span> 
                                  </span>
                                  <p className="text-gray-400 fs-4 fw-bold py-3">No orders found.</p>
                                </div>
                                :
                                <div className="table-responsive">
                                  <table className="table table-row-dashed table-row-gray-200 align-middle gs-0 gy-4">
                                    
                                    <thead className="fs-7 text-gray-400 text-uppercase">
                                      <tr>
                                        <th className="min-w-20px text-center">Currency</th>
                                        <th className="min-w-90px"></th>
                                        <th className="min-w-50px">USD Amount</th>
                                        <th className="min-w-60px">Rate</th>
                                        <th className="min-w-120px">Total</th>
                                        <th className="min-w-90px">Date</th>
                                        <th className="min-w-50px">Status</th>
                                        <th className="min-w-50px text-end"></th>
                                      </tr>
                                    </thead>

                                    <tbody>
                                      {
                                        this.state.orders?.map((data, index) => {
                                          let ghs = parseFloat(data.fee * data.rate) + parseInt(data.amount * data.rate)
                                          let date = format(`${data.updatedAt}`)
                                          return (
                                            <tr key={index}>
                                              <td className="text-center">
                                                <div className="symbol symbol-45px me-2">
                                                  {
                                                    data?.asset === 'BTC' ?
                                                    <span className="symbol-label bg-light-warning">
                                                      <img src="/assets/media/svg/brand-logos/btc.svg" className="h-50 align-self-center" alt="" />
                                                    </span>
                                                    :
                                                    <span className="symbol-label bg-light-success">
                                                      <img src="/assets/media/svg/brand-logos/usdt.svg" className="h-50 align-self-center" alt="" />
                                                    </span>
                                                  }
                                                </div>
                                              </td>
                                              <td>
                                                <small className="text-dark fw-bolder text-hover-primary mb-1 fs-6">{data?.asset} 
                                                  {
                                                    data.type==='buy' ?
                                                    <span className="badge badge-light-primary ms-2">Buy Order</span>
                                                    :
                                                    <span className="badge badge-light-dark ms-2">Sell Order</span>
                                                  }
                                                  
                                                </small>
                                              </td>
                                              <td><span className="text-dark fw-bold d-block">${data.amount} {data.type==='buy' && <>• <span className="text-muted">Fees:</span> ${data.fee}</>}</span></td>
                                              <td>
                                                <small className="text-dark fw-bolder text-hover-primary mb-1">GHS {data.rate}</small>
                                              </td>
                                              <td>
                                                <small className="text-dark fw-bolder text-hover-primary mb-1">GHS {ghs}</small>
                                              </td>
                                              <td className="text-start text-muted fw-bold">{date}</td>
                                              <td className="text-start">
                                                {
                                                  ["pending_payment","pending_verification","pending_transfer","pending_payout", "pending"].includes(data.status) &&
                                                  <span className="badge badge-light-warning">Pending</span>
                                                }

                                                {
                                                  data.status?.includes('awaiting') &&
                                                  <span className="badge badge-light-success">Waiting approval</span>
                                                }

                                                {
                                                  data.status === 'completed' &&
                                                  <span className="badge badge-light-primary">Completed</span>
                                                }
                                                
                                                {
                                                  data.status === 'refunded' &&
                                                  <span className="badge badge-light-danger">Refunded</span>
                                                }
                                                
                                                {
                                                  data.status === 'cancelled' &&
                                                  <span className="badge badge-light-danger">Cancelled</span>
                                                }
                                                
                                              </td>
                                              <td className="text-end">
                                                <button className="btn btn-sm btn-icon btn-bg-light btn-active-color-primary" onClick={() => {
                                                  this.setState({
                                                    ...this.state,
                                                    orderID: data.id,
                                                    showOrderInfo: true,
                                                    passedOrder: data
                                                  })

                                                  setTimeout(() => {
                                                    window.modalShow('order_info_modal');
                                                  }, 500);
                                                }}>
                                                  
                                                  <span className="svg-icon svg-icon-2">
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                                                      <rect opacity="0.5" x="18" y="13" width="13" height="2" rx="1" transform="rotate(-180 18 13)" fill="black" />
                                                      <path d="M15.4343 12.5657L11.25 16.75C10.8358 17.1642 10.8358 17.8358 11.25 18.25C11.6642 18.6642 12.3358 18.6642 12.75 18.25L18.2929 12.7071C18.6834 12.3166 18.6834 11.6834 18.2929 11.2929L12.75 5.75C12.3358 5.33579 11.6642 5.33579 11.25 5.75C10.8358 6.16421 10.8358 6.83579 11.25 7.25L15.4343 11.4343C15.7467 11.7467 15.7467 12.2533 15.4343 12.5657Z" fill="black" />
                                                    </svg>
                                                  </span>
                                                  
                                                </button>
                                              </td>
                                            </tr>
                                          )
                                        })
                                      }
                                      
                                    </tbody>
                                    
                                  </table>
                                  
                                </div>
                              }
                              </div>
                            </div>
                            </>
                          }
                          <div className="pt-10" style={{opacity: `${this.state.isRequesting?'0.5':'1'}`, pointerEvents: `${this.state.isRequesting?'none':''}`}}>
                            {
                              this.state.total >= 5 && <Pagination1 fetch={(count)=>this.fetchByPagination(count)} total={this.state.total}/>
                            }
                          </div>
                        </div>
                      </div>
                      <Footer />
                    </>
                  }
                </div>
                
              </div>
              
            </div>
          </div>
          <OrderForm type={this.state.orderType} callback={true} showNewOrder={this.showOrderInfo} setPassOrder={(data)=>
            this.setState({
              ...this.state,
              passedOrder: data
            })} 
            setOrderId={(id)=>{
              this.setState({
              ...this.state,
              orderID: id
              })
            }} 
            />
          {
            this.state.showOrderInfo && 
            <OrderInfo orderID={this.state.orderID} callback={true} passedOrder={this.state.passedOrder} orderUpdate={this.updateOrders} />
          }
      </Fragment>
    );
  }
}

export default Orders;