import React, { Fragment } from "react";
import MetaTags from "react-meta-tags";
import AuthService from '../../misc/AuthService';
import CONSTANTS from '../../misc/Constants';
import axios from 'axios';
class ForgotPassword extends React.Component {

  constructor(props){
		AuthService.checkGreeter();
    super(props);
    this.state = {
			isLoading: false,
      showForgotPasswordForm: true,
      isRequesting: false,
			email: '',
      errorMessage: ''
    }
	}
	
	forgotPasswordForm(event, name) {
    let value = event.target.value;
    this.setState({
      ...this.state,
      email: value,
    });
  }
	
	submitForgotPasswordForm = e => {
    this.setState({
      ...this.state,
      isRequesting: true,
      errorMessage: '',
    });
    e.preventDefault();

    axios.post(CONSTANTS.API_BASE_URL + "/auth/user/forgot/password", {email: this.state.email})
    .then(() => {
      this.setState({
        ...this.state,
        isRequesting: false,
        showForgotPasswordForm: false
      });

    }).catch((error) => {
      try{
        let errorResponse = error.response.data;

        let errorMessage = 'Error: Could not connect to server';
        if(errorResponse.hasOwnProperty("message")){
          errorMessage = errorResponse.message;
        }

        this.setState({
          ...this.state,
          isRequesting: false,
          errorMessage: errorMessage
        });
      }catch(e){
        window.location = "/server-offline";
      }
    });

  }

  componentDidMount (){
		// window.initScript(window.NioApp, window.jQuery);
  }

  render(){
    return (
      <Fragment>
          <MetaTags>
          <title>Forgot Password - HodlPay</title>
          </MetaTags>
          <div className="d-flex flex-column flex-root">
            <div className="d-flex flex-column flex-column-fluid bgi-position-y-bottom position-x-center bgi-no-repeat bgi-size-contain bgi-attachment-fixed">
              <div className="d-flex flex-center flex-column flex-column-fluid p-10 pb-lg-20">
                <a href="/login" className="mb-10 pt-lg-10">
                  <img alt="Logo" src="/assets/media/logos/hodlpay-black.svg" className="h-70px" />
                </a>
                {
                  this.state.showForgotPasswordForm ?
                  <div className="w-lg-500px bg-body rounded shadow-sm p-10 p-lg-15 mx-auto">
                    {
                      this.state.errorMessage.length > 0 &&
                      <div className="alert alert-danger d-flex align-items-center p-5 mb-10 w-100"> 
                        <span className="svg-icon svg-icon-2hx svg-icon-danger me-4">
                          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                            <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                                <rect x="0" y="0" width="24" height="24"/>
                                <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"/>
                                <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"/>
                                <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"/>
                            </g>
                          </svg>
                        </span>
                        <div className="d-flex flex-column">
                          <h4 className="mb-1 text-danger">Error</h4> <span>{this.state.errorMessage}</span> </div>
                      </div>
                    }

                    <form className="form w-100" onSubmit={this.submitForgotPasswordForm} method="POST">
                      <div className="text-center mb-10">
                        <h1 className="text-dark mb-3">Forgot Password ?</h1>
                        <div className="text-gray-400 fw-bold fs-4">Enter your email to reset your password.</div>
                      </div>
                      <div className="fv-row mb-10">
                        <label className="form-label fw-bolder text-gray-900 fs-6">Email</label>
                        <input className="form-control form-control-solid" type="email" placeholder="" name="email" value={this.state.email} onChange={(e) => {this.forgotPasswordForm(e,"email");}} />
                      </div>
                      
                      <div className="d-flex flex-wrap justify-content-center pb-lg-0">
                        {
                          this.state.isRequesting ?
                          <button type="button" className="btn btn-lg btn-primary mx-2" disabled>
                            <span className="">Please wait... 
                            <span className="spinner-border spinner-border-sm align-middle ms-2"></span></span>
                          </button>
                          :
                          <button type="submit" className="btn btn-lg btn-primary">
                            <span className="indicator-label">Submit</span>
                          </button>
                        }
                        <a href="/login" className="btn btn-lg btn-light-primary fw-bolder mx-2">Back to Sign In</a>
                        
                      </div>
                    </form>
                  </div>
                  :
                  <div className="pt-lg-10 mb-10 text-center">
                    <h1 className="fw-bolder fs-2qx text-gray-800 mb-7">Password Reset</h1>
                    <div className="fs-3 fw-bold text-muted mb-10">We have sent an email to 
                    <small className="link-primary fw-bolder"> {this.state.email}</small>
                    <br />please follow a link to reset your password.</div>
                    <div className="text-center mb-10">
                      <a href="/login" className="btn btn-lg btn-primary fw-bolder">Skip for now</a>
                    </div>
                    {/* <div className="fs-5">
                      <span className="fw-bold text-gray-700">Did’t receive an email?</span>
                      <a href="" className="link-primary fw-bolder">Resend</a>
                    </div> */}
                  </div>
                }
              </div>
              <div className="d-flex flex-center flex-column-auto p-10">
                <div className="d-flex align-items-center fw-bold fs-6">
                  <a href={`${CONSTANTS.WEB_URL}/#about`} target="_blank" className="text-muted text-hover-primary px-2" rel="noreferrer">About</a>
                  <a href={`${CONSTANTS.WEB_URL}/terms-and-conditions`} target="_blank" className="text-muted text-hover-primary px-2" rel="noreferrer">Terms and Conditions</a>
                  <a href={`${CONSTANTS.WEB_URL}/privacy-policy`} target="_blank" className="text-muted text-hover-primary px-2" rel="noreferrer">Privacy Policy</a>
                </div>
              </div>
            </div>
          </div>
      </Fragment>
    );
  }
}

export default ForgotPassword;