import React from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Login from './components/client/Login';
import AdminLogin from './components/admin/Login';
import AdminDashboard from './components/admin/Dashboard';
import AdminOrders from './components/admin/Orders';
import AdminSettings from './components/admin/Settings';
import Logout from './components/client/Logout';
import Registration from './components/client/Registration';
import EmailVerification from './components/client/EmailVerification';
import PhoneNumberVerification from './components/client/PhoneNumberVerification';
import ForgotPassword from './components/client/ForgotPassword';
import ResetPassword from './components/client/ResetPassword';
import Dashboard from './components/client/Dashboard';
import Orders from './components/client/Orders';
import Faq from './components/client/Faq';
import UserSettings from './components/client/UserSettings';
import NotFound from './components/client/NotFound';
import Expiry from './misc/sessionExpiry';
import cookie from 'react-cookies'
import AdminUsers from './components/admin/Users';

class Routes extends React.Component {
  componentDidMount(){
    if(cookie.load('isLogin') === 'true'){
      Expiry()
    }else{
      if(!(window.location.href).includes('login') && (window.location.href).includes('admin')){
        if(!(window.location.href).includes('register')){
          window.location = '/admin/login'
        }
      }
      if(!(window.location.href).includes('login') && !(window.location.href).includes('admin') && (window.location.href).includes('register')){
        if(!(window.location.href).includes('register')){
          window.location = '/admin/login'
        }
      }
    }
  }
  render(){
    return (
      <Router>
        <Switch>
          
          <Route exact path={`${process.env.PUBLIC_URL + "/"}`} component={Login} />
          <Route exact path={`${process.env.PUBLIC_URL + "/login"}`} component={Login} />
          <Route exact path={`${process.env.PUBLIC_URL + "/register"}`} component={Registration} />
          <Route exact path={`${process.env.PUBLIC_URL + "/email-verification/code/:token"}`} component={EmailVerification} />
          <Route exact path={`${process.env.PUBLIC_URL + "/email-verification/check/:email"}`} component={EmailVerification} />
          <Route exact path={`${process.env.PUBLIC_URL + "/phone-verification"}`} component={PhoneNumberVerification} />
          <Route exact path={`${process.env.PUBLIC_URL + "/forgot-password"}`} component={ForgotPassword} />
          <Route exact path={`${process.env.PUBLIC_URL + "/reset-password/:token"}`} component={ResetPassword} />
          <Route exact path={`${process.env.PUBLIC_URL + "/dashboard"}`} component={Dashboard} />
          <Route exact path={`${process.env.PUBLIC_URL + "/orders"}`} component={Orders} />
          <Route exact path={`${process.env.PUBLIC_URL + "/faq"}`} component={Faq} />
          <Route exact path={`${process.env.PUBLIC_URL + "/account-settings"}`} component={UserSettings} />
          {/*<Route exact path={`${process.env.PUBLIC_URL + "/register/e/:email"}`} component={Registration} />
          
          
          <Route exact path={`${process.env.PUBLIC_URL + "/forgot-password-verification/:email"}`} component={ForgotPasswordVerification} />
          <Route exact path={`${process.env.PUBLIC_URL + "/reset-password/:token"}`} component={ResetPassword} />
          <Route exact path={`${process.env.PUBLIC_URL + "/password-reset/:status"}`} component={ForgotPasswordVerification} />*/}
          <Route exact path={`${process.env.PUBLIC_URL + "/admin/login"}`} component={AdminLogin} /> 
          <Route exact path={`${process.env.PUBLIC_URL + "/admin/dashboard"}`} component={AdminDashboard} /> 
          <Route exact path={`${process.env.PUBLIC_URL + "/admin/orders"}`} component={AdminOrders} /> 
          <Route exact path={`${process.env.PUBLIC_URL + "/admin/users"}`} component={AdminUsers} /> 
          <Route exact path={`${process.env.PUBLIC_URL + "/admin/settings"}`} component={AdminSettings} /> 
          <Route exact path={`${process.env.PUBLIC_URL + "/logout"}`} component={Logout} /> 
          <Route path={`${process.env.PUBLIC_URL + "/not-found"}`} component={NotFound} />
          <Route exact component={NotFound} />
        </Switch>
      </Router>
    )
  }
}


export default Routes;