import axios from 'axios';
import cookie from 'react-cookies'
import Constants from './Constants';


function Expiry (){
    setInterval(()=>{
        let current = parseInt(sessionStorage.getItem('currentCount')) || 1000;
        let refresh_token = cookie.load('refresh_token')
        if(current >= 720000){
        sessionStorage.setItem('currentCount', 1000)
        axios.post(`${Constants.API_BASE_URL}/auth/refresh/token`, {
            'refreshToken': refresh_token
        }).then(res=>{
            console.log(res.data)
            cookie.save('access_token', res.data.token)
            cookie.save('admin_access_token', res.data.token)
            cookie.save('refresh_token', res.data.refreshToken)
            sessionStorage.setItem('currentCount', 1000)
        }).catch(()=>{
            let loc = window.location.href;
            if(loc.includes('admin')){
                window.location = '/admin/login'
            }else{
                window.location = '/logout'
            }
        })
        }else{
            sessionStorage.setItem('currentCount', current+2000)
        }
            clearInterval()
        }, 2000)
}
export default Expiry