import React, { useState } from 'react'

function Pagination1({fetch, total}) {
    const [count, setcount] = useState(1)
    const [limit, setLimit] = useState(count+2)
    let totall = 100;
    let list = []
    for(let start = 1; start < totall; start ++){
        list.push(
            <li key={start} className={`btn btn-${count === start ? 'primary': 'outline-primary'} p-3 m-2`} onClick={()=>setCountHandler(start)}>{start}</li>
        )
    }
    function setCountHandler (val){
        setcount(()=>val)
        setTimeout(() => {
            fetch(val)
            if(val !== count){
                setLimit(count+3)
            }
        }, 500);
    }

    const setPage = (page) => {
        if(page === 'f' && count !== totall){
            let target = count+1;
            setCountHandler(target)
        }
        if(page === 'b' && count !== 1){
            let target = count-1;
            setCountHandler(target)
        }
    }
    return (
        <div className='flex' style={{justifyContent:'center' }}>
            <ul id='pageNumber' className='flex'>
                <li onClick={()=>setPage('b')} className='m-2 p-5'>{'<'}</li>
                {list.map((val,idx)=>{
                    let current = idx+1;
                    if((current >= count) && (current < limit)){
                        return val
                    }
                    return false
                })}
                <li onClick={()=>setPage('f')} className='m-2 p-5'>{'>'}</li>
            </ul>
        </div>
    )
}

export default Pagination1