import React, { useEffect, useState } from 'react'

function Pagination1({fetch, total}) {
    const [count, setcount] = useState(1)
    const [limit, setLimit] = useState(count+2)
    let totall = 100;
    let list = []
    for(let start = 1; start < totall; start ++){
        list.push(
            <li key={start} className={`btn btn-${count === start ? 'primary': 'outline-primary'} p-3 m-2`} onClick={()=>setCount(start)}>{start}</li>
        )
    }
    useEffect(()=>{
        if(count+3 < total){
            setLimit(count+3)
        }else{
            if(count+1 < total+1)
            setLimit(count+1)
        }
       
        console.log('total', total)
        // eslint-disable-next-line
    }, [count])
    

    const setCount = (count) => {
        setcount(count)
        return fetch(count)
    }

    const setPage = (page) => {
        if(page === 'f' && count !== total){
            if(count+3 <= total)
                setcount(prev=>prev+1)
        }
        if(page === 'b' && count !== 1){
            setcount(prev=>prev-1)
        }
    }
    return (
        <div className='flex' style={{justifyContent:'center' }}>
            <ul id='pageNumber' className='flex'>
                <li onClick={()=>setPage('b')} className='m-2 p-5'>{'<'}</li>
                {list.map((val,idx)=>{
                    let current = idx+1;
                    if((current >= count) && (current < limit)){
                        return val
                    }
                    return false
                })}
                <li onClick={()=>setPage('f')} className='m-2 p-5'>{'>'}</li>
            </ul>
        </div>
    )
}

export default Pagination1