import cookie from 'react-cookies';
import { reactLocalStorage } from 'reactjs-localstorage';

const AuthService =  {
    checkProtected: () => {
        var access_token = cookie.load('access_token');
				var userObj = reactLocalStorage.getObject('userObj');

        if(access_token === undefined || access_token  === ""){
					window.location = "/logout"
					return;
				}

				if(JSON.stringify(userObj) === '{}'){
					window.location = "/logout";
					return;
          
				}else if(!userObj?.emailIdentity?.emailVerified){
          window.location = "/email-verification/check/"+userObj?.emailIdentity?.email;
          return;

        }else if(!userObj?.phoneIdentity?.phoneVerified){
          window.location = "/phone-verification";
          return;
        }
				
    },

		checkAdminProtected: () => {
			var access_token = cookie.load('admin_access_token');
			if(access_token === undefined || access_token  === ""){
				window.location = "/logout"
				return;
			}
		},

    getAuth: () => {
        var userObj = cookie.load('userObj');
        var access_token = cookie.load('access_token');
        if(JSON.stringify(userObj) === '{}' || access_token === ''){
            window.location = "/logout";
						return;
        }   
        
        return {
          "user": userObj,
          "access_token": access_token
        }
    },

    getAdminAuth: () => {
      var adminObj = cookie.load('adminObj');
      var admin_access_token = cookie.load('admin_access_token');
      
      return {
        "admin": adminObj,
        "admin_access_token": admin_access_token
      }
    },

    checkGreeter: () => {
        // var access_token = cookie.load('access_token');
        // var userObj = reactLocalStorage.getObject('userObj');
        // if(access_token !== undefined && userObj?.accountStatus){
        //   window.location = "/dashboard";
        //   return;
        // }
        // if(access_token !== undefined && JSON.stringify(userObj) !== '{}'){
        //   window.location = "/dashboard";
        //   return;
        // }
    },

    checkAdminGreeter: () => {
      var access_token = cookie.load('admin_access_token');
      var adminObj = reactLocalStorage.getObject('adminObj');
      if(access_token !== undefined && adminObj.account_status){
        window.location = "/admin/dashboard";
        return;
      }
    },

    getAxiosHeaders: () => {
        var access_token = cookie.load('access_token');
        return { headers: {"Authorization" : `Bearer ${access_token}`} }
    },

		getAxiosAdminHeaders: () => {
			var access_token = cookie.load('admin_access_token');
			return { headers: {"Authorization" : `Bearer ${access_token}`} }
		},

    getAxiosAdminUploadHeaders: () => {
      var access_token = cookie.load('admin_access_token');
      return { 
          headers: {
              "Authorization" : `Bearer ${access_token}`, 
              "content-type": "multipart/form-data"
          } 
      }
    },

    getAxiosUploadHeaders: () => {
        var access_token = cookie.load('access_token');
        return { 
            headers: {
                "Authorization" : `Bearer ${access_token}`, 
                "content-type": "multipart/form-data"
            } 
        }
    }
};

export default AuthService;