import React, { Fragment } from "react";
import MetaTags from "react-meta-tags";
import CONSTANTS from '../../misc/Constants';
import axios from 'axios';
import cookie from 'react-cookies';
import { reactLocalStorage } from 'reactjs-localstorage';
class AdminLogin extends React.Component {

  constructor(props){
		// AuthService.checkAdminGreeter();
    super(props);
    this.state = {
			isLoading: false,
			isRequesting: false,
      showPassword: false,
			loginForm: {
				email: '',
      	password: '',
			},
      errorMessage: ''
    }
	}
	
	loginForm(event, name) {
    let value = event.target.value;
    var oldData = this.state.loginForm;
    oldData[name] = value;
    this.setState({
			...this.state,
      loginForm: oldData,
    });
	}
	
	submitLoginForm = e => {
		this.setState({
			...this.state,
			isRequesting: true,
			errorMessage: '',
		});
		e.preventDefault();
    
		axios.post(CONSTANTS.API_BASE_URL + "/auth/user/login", {...this.state.loginForm})
		.then((response) => {
      let data = response.data;
      sessionStorage.setItem('currentCount', 1000)

      cookie.save('isLogin', true)
      cookie.save('admin_access_token', data.token)
      cookie.save('refresh_token', data.refreshToken)
      reactLocalStorage.setObject('adminObj', {phoneIdentity: data.phoneIdentity, emailIdentity: data.emailIdentity, user: data.user, accountStatus: true});
      cookie.save('adminObj', {phoneIdentity: data.phoneIdentity, emailIdentity: data.emailIdentity, user: data.user, accountStatus: true})
      
      console.log(data, cookie.load('adminObj'))
      window.location = "/admin/dashboard";
		}).catch((error) => {
      console.log('Error : ', error.response)
      try{
        let errorMessage = 'Error: Could not connect to server';

        this.setState({
          ...this.state,
          isRequesting: false,
          errorMessage: errorMessage,
        });
      }catch(e){
        return
      }
		});

	}

  componentDidMount (){
    cookie.save('isLogin', false)
    cookie.remove('admin_access_token');
    cookie.remove('access_token');
    cookie.remove('userObj');
    cookie.remove('phoneIdentity');
    cookie.remove('userOrders');
		// window.initScript(window.NioApp, window.jQuery);
  }

  render(){
    return (
      <Fragment>
          <MetaTags>
          <title>Admin Login - HodlPay</title>
          </MetaTags>
          <div className="d-flex flex-column flex-root bg-dark h-100">
            <div className="d-flex flex-column flex-column-fluid bgi-position-y-bottom position-x-center bgi-no-repeat bgi-size-contain bgi-attachment-fixed">
              <div className="d-flex flex-center flex-column flex-column-fluid p-10 pb-lg-20">
                <a href="/admin/login" className="mb-10 pt-lg-10">
                  <img alt="Logo" src="/assets/media/logos/hodlpay-white.svg" className="h-70px" />
                </a>
                
                
                <div className="w-lg-500px bg-body rounded shadow-sm p-10 p-lg-15 mx-auto">
                  <div className="text-center mb-10">
                    <h1 className="text-dark mb-3">Sign In to Admin Portal</h1>
                  </div>
                  {
                    this.state.errorMessage.length > 0 &&
                    <div className="alert alert-danger d-flex align-items-center p-5 mb-10 w-100"> 
                      <span className="svg-icon svg-icon-2hx svg-icon-danger me-4">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none">
                          <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                              <rect x="0" y="0" width="24" height="24"/>
                              <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10"/>
                              <rect fill="#000000" x="11" y="10" width="2" height="7" rx="1"/>
                              <rect fill="#000000" x="11" y="7" width="2" height="2" rx="1"/>
                          </g>
                        </svg>
                      </span>
                      <div className="d-flex flex-column">
                        <h4 className="mb-1 text-danger">Error</h4> <span>{this.state.errorMessage}</span> </div>
                    </div>
                  }
                  <form onSubmit={this.submitLoginForm} method="POST">
                    
                    <div className="fv-row mb-10">
                      <label className="form-label fs-6 fw-bolder text-dark">Email</label>
                      <input className="form-control form-control-lg form-control-solid" type="text" name="email" autoComplete="off" value={this.state.loginForm.email} onChange={(e) => {this.loginForm(e,"email");}}/>
                    </div>
                    <div className="mb-10 fv-row" data-kt-password-meter="true">
                      <div className="mb-1">
                        <div className="d-flex flex-stack mb-2">
                          <label className="form-label fw-bolder text-dark fs-6">Password</label>
                          <a href="password-reset.html" className="link-primary fs-6 fw-bolder">Forgot Password ?</a>
                        </div>
                        <div className="position-relative mb-3">
                          <input className="form-control form-control-lg form-control-solid" type={this.state.showPassword ? "text" : "password"} placeholder="" name="password" autoComplete="off" value={this.state.loginForm.password} onChange={(e) => {this.loginForm(e,"password");}} />
                          <span className="btn btn-sm btn-icon position-absolute translate-middle top-50 end-0 me-n2" data-kt-password-meter-control="visibility" onClick={() => {
                            this.setState({
                              ...this.state,
                              showPassword: this.state.showPassword ? false : true
                            })
                          }}>
                            {
                              this.state.showPassword ?
                              <i className="bi bi-eye-slash fs-2"></i>
                              :
                              <i className="bi bi-eye fs-2"></i>
                            }
                          </span>
                        </div>
                      </div>
                    </div>
                    <div className="text-center">
                    {
                      this.state.isRequesting ?
                      <button type="button" className="btn btn-lg btn-primary w-100" disabled>
                        <span className="">Please wait... 
                        <span className="spinner-border spinner-border-sm align-middle ms-2"></span></span>
                      </button>
                      :
                      <button type="submit" className="btn btn-lg btn-primary w-100">
                        <span className="indicator-label">Sign In</span>
                      </button>
                    }
                    </div>
                  </form>
                </div>
              </div>
              <div className="d-flex flex-center flex-column-auto p-10">
                <div className="d-flex align-items-center fw-bold fs-6">
                  <a href={`${CONSTANTS.WEB_URL}/#about`} target="_blank" className="text-muted text-hover-primary px-2" rel="noreferrer">About</a>
                  <a href={`${CONSTANTS.WEB_URL}/terms-and-conditions`} target="_blank" className="text-muted text-hover-primary px-2" rel="noreferrer">Terms and Conditions</a>
                  <a href={`${CONSTANTS.WEB_URL}/privacy-policy`} target="_blank" className="text-muted text-hover-primary px-2" rel="noreferrer">Privacy Policy</a>
                </div>
              </div>
            </div>
          </div>
      </Fragment>
    );
  }
}

export default AdminLogin;