  getDashboardStats () {

    let assets = cookie.load('assets')
    this.setState({
      ...this.state,
      isLoading: true,
      dashboardStats: {
        ...this.state.dashboardStats,
        rates_info : {
          bitcoin_buy_rate: assets[0]?.code === 'BTC' ? assets[0]?.data?.buyRate : assets[1]?.data?.buyRate,
          bitcoin_sell_rate: assets[0]?.code === 'BTC' ? assets[0]?.data?.sellRate : assets[1]?.data?.sellRate,
          usdt_buy_rate: assets[0]?.code?.includes('USDT') ? assets[0]?.data?.buyRate : assets[1]?.data?.buyRate,
          usdt_sell_rate: assets[0]?.code?.includes('USDT') ? assets[0]?.data?.sellRate : assets[1]?.data?.sellRate,
          buy_orders_normal_fee: '0.00',
          buy_orders_priority_fee: '0.00',
        }
      }
    })

    
    // Getting all orders...
    axios.get(`${Constants.API_BASE_URL}/admin/orders`, AuthService.getAxiosAdminHeaders())
    .then(res=>{
      let data = res.data;
      
      console.log(data)
    }).catch(error=>{
      console.clear()
      try{
        let errorResponse = error.response.data;

        if(error.response.status === 401){
          refreshToken('admin')
          return
        }

        let errorMessage = 'Error: Could not connect to server';
        if(errorResponse.hasOwnProperty("message")){
          errorMessage = errorResponse.message;
        }

        this.setState({
          ...this.state,
          isLoading: false,
          errorMessage: errorMessage
        });
      }catch(e){
        console.log(error.message)
        window.location = "/server-offline";
      }
    })

    // Getting orders Stats ...
    axios.get(`${Constants.API_BASE_URL}/admin/orders/stats`, AuthService.getAxiosAdminHeaders())
    .then(res=>{
      let data = res.data;

      this.setState({
        ...this.state,
        dashboardStats: {
          ...this.state.dashboardStats,
          buy_orders_stats:{
            ...this.state.dashboardStats.buy_orders_stats,
            total_amount: data.buyOrders.totalBuyOrdersAmount,
            total_orders: data.buyOrders.buyOrders,
            cryptocurrency: {
              ...this.state.dashboardStats.buy_orders_stats.cryptocurrency,
              bitcoin: {
                amount: data.buyOrders.totalBitcoinBuyOrders,
                percentage: parseFloat(data.buyOrders.totalBitcoinBuyOrders)/100
              },
              usdt: {
                amount: data.buyOrders.totalUSDTbuyOrders,
                percentage: parseFloat(data.buyOrders.totalUSDTbuyOrders)/100
            },
            }
          },
          sell_orders_stats:{
            ...this.state.dashboardStats.sell_orders_stats,
            total_amount: data.sellOrders.totalSellOrdersAmount,
            total_orders: data.sellOrders.sellOrders,
            cryptocurrency: {
              ...this.state.dashboardStats.sell_orders_stats.cryptocurrency,
              bitcoin: {
                amount: data.sellOrders.totalBitcoinSellOrders,
                percentage: parseFloat(data.sellOrders.totalBitcoinSellOrders)/100
              },
              usdt: {
                amount: data.sellOrders.totalUSDTsellOrders,
                percentage: parseFloat(data.sellOrders.totalUSDTsellOrders)/100
            },
            }
          },
          pending_orders: {
            pending_buy_orders: data.buyOrders.buyPendingOrders,
            pending_sell_orders: data.sellOrders.totalSellPendingOrders,
          },
        }
      })
      console.log(data)
    }).catch(()=>{
      this.setState({...this.state, isLoading: false})
    })

    // Getting all assets ...
    axios.get(`${Constants.API_BASE_URL}/assets`, AuthService.getAxiosAdminHeaders())
    .then(res=>{
      let data = res.data;
      cookie.save('assets', data)
    }).catch((error)=>{
      console.clear()
      try{
        let errorResponse = error.response.data;

        if(error.response.status === 401){
          refreshToken('admin')
          return
        }

        let errorMessage = 'Error: Could not connect to server';
        if(errorResponse.hasOwnProperty("message")){
          errorMessage = errorResponse.message;
        }

        this.setState({
          ...this.state,
          isLoading: false,
          errorMessage: errorMessage
        });
      }catch(e){
        console.log(error.message)
        window.location = "/server-offline";
      }
    })

    // Getting all users ...
    axios.get(`${Constants.API_BASE_URL}/admin/users/stats`, AuthService.getAxiosAdminHeaders())
    .then(res=>{
      let data = res.data;
      this.setState({
        ...this.state,
        isLoading: false,
        dashboardStats: {
          ...this.state.dashboardStats,
          users_stats: {
            ...this.state.dashboardStats.users_stats,
            total_active_users: data.activeUsersThisMonth,
            total_users: data.totalUsers
          }
        }
      })
    }).catch((error)=>{
      console.clear()
      try{
        let errorResponse = error.response.data;

        if(error.response.status === 401){
          refreshToken('admin')
          return
        }

        let errorMessage = 'Error: Could not connect to server';
        if(errorResponse.hasOwnProperty("message")){
          errorMessage = errorResponse.message;
        }

        this.setState({
          ...this.state,
          isLoading: false,
          errorMessage: errorMessage
        });
      }catch(e){
        console.log(error.message)
        window.location = "/server-offline";
      }
    })

    

  }

